.Searchbar {
  display: flex;
  align-items: center;
  border-width: 2px;
  background-color: white;
  width: 100%;
  height: auto;
  max-height: 40%;
  // border-radius:9999px;
  padding: 2px;
  padding-top: 5px;
  padding-bottom: 5px;
  // border: 2px solid lightgrey;
  position: fixed;
  top: 0;
  box-shadow: 0 0 0 100vh rgba(0, 0, 0, 0.4);
  z-index: 5;
  left: 0;
  .formSearch {
    display: flex;
    flex-direction: column;

    .locInp {
      border: 1px solid black;
      border-radius: 10px;
      margin: 20px 50px;
      padding: 30px;

      .searchbarInput {
        position: relative;
        border: transparent;
      }
    }

    .btnCity {
      background-color: transparent;
      border: none;
    }



  }
  .formguest {
      display: flex;
      flex-direction: column;
      gap: 1rem;

      .gueInp{
        display: flex;
        flex-direction: column;
        border: 1px solid black;
        border-radius: 10px;
        // margin: 20px 50px;
        padding: 30px;

        .guestBarInput{
          position: relative;
          border: transparent;
        }
      }
    }
    .inpFormAdCh{
      width: 1rem;
      text-align: center;
      border: none;
      font-weight: bold;
    }
    
    .plusMin
    {
      background:#ffffff;
      border: 1px solid rgba(112, 112, 112, 0.6);
      border-radius: 5px;
    }
    
    
    .aduChi {
      display: flex;
      flex-direction: column;
    }
    .Icona {
      display: wrap;
      display:flex;
      align-items:flex-start;
      align-items:center;
      border-width: 2px;
      border-radius:9999px;
      padding:2px;
      padding-top:5px;
      padding-bottom:5px;
      border: 2px solid lightgrey;
      position:relative;


     .searchIcon {

        height: 12px;
        color:white;
        background-color:rgb(219, 80, 80);
       border-radius:50%;
       padding: 5px;
       cursor: pointer;

       position: relative;
     }
    }
  }
  
  
